//Confirmed
let cnf = {};
let cnf_mx = ["50","300","500","1000","3000","5000","8000","12000","20000","30000","1200000"];
let cnf_code = ["#ffcccc","#ffb3b3","#ff9999","#ff8080","#ff6666","#ff3333",
                "#ff0000","#b30000","#800000","#4d0000","#1a0000"];
cnf['color'] =  [];
cnf_mx.forEach((element,id) => {
    cnf['color'].push({"maxvalue" : element, "code" : cnf_code[id]});
});
cnf['title'] = 'Confirmed Cases';
cnf['data'] = [];

