import React from "react";
import { CSSTransition } from 'react-transition-group';
import CommaNumber from 'comma-number';
import {
  Card,
  CardHeader,
  CardTitle,
  Row,
  Col
} from "reactstrap";


class NumberPanel extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            showMessage : true
        };
    }

    int = (str) => {
        let e = "";
        for(let i=0; i<str.length; i++)
        {
            if(str[i] - '0' >= 0 || str[i] - '0' <= 9) e+=str[i];
        }
        return Number.parseInt(e);
    }
    
    render(){
        if(this.props.data == null) return null;
        let confirmed = this.props.data['TotalCases'];
        let deceased = this.props.data['TotalDeaths'];
        let recovered = this.props.data['TotalRecovered'];
        let active = CommaNumber(this.int(this.props.data['TotalCases']) 
                        - this.int(this.props.data['TotalDeaths']) 
                        - this.int(this.props.data['TotalRecovered']));
        let dcon = this.int(this.props.data['NewCases']);
        let drec = this.int(this.props.data['NewRecovered']);
        let ddec = this.int(this.props.data['NewDeaths']);
        let dact = dcon - drec - ddec;
        let dconfirmed = "[ "+this.props.data['NewCases']+" ]";
        let ddeceased = "[ "+this.props.data['NewDeaths']+" ]";
        let drecovered = "[ "+this.props.data['NewRecovered']+" ]";
        let dactive = "[ "+((dact > 0) ? "+" : "") + CommaNumber(dact)+" ]";
        let listContent = [
            [3, "Confirmed", "text-danger", confirmed, dconfirmed],
            [3, "Active", "text-primary", active, dactive],
            [3, "Recovered", "text-success", recovered, drecovered],
            [3, "Deceased", "text-defalut", deceased, ddeceased]
        ];
        //const link
        const list = listContent.map((ele, key) => {
            let link = null, update= null;
            update = <div style={{fontSize : "15px"}}>{ele[4]}</div>;
            return (
                <Col lg={ele[0]} key={key}>
                <Card className="card-chart">
                    <CardHeader>
                    <h1 className="card-category">
                        <span className="card-big-text"> {ele[1]} </span>
                    </h1>
                    {update}
                    <CardTitle tag="h4">
                        <i className={"tim-icons icon-world "+ele[2]} />{" "}
                        {ele[3]}
                    </CardTitle>
                    </CardHeader>
                </Card>
                </Col>
            );
        });
        return (
      
            <>
                <CSSTransition
                    in={this.state.showMessage}
                    timeout={200}
                    appear={true}
                    classNames="fader"
                >
                    <Row>
                        {list}
                    </Row>
                </CSSTransition>
            </>

        );
    }
}

export default NumberPanel;