import React from "react";
import Axios from 'axios';
import NumberPanel from "./segments/NumberPanel";
import AllInOneGraph from "./segments/AllInOneGraph";
import Top10India from "./segments/Top10India";
import IndiaMap from "./segments/IndiaMap";
import StateWiseData from "./segments/StateWiseData";
import {cnf, rec, dec, pop, ppm, mor, tst, tpm} from './segments/MapData';
import {BrowserView} from 'react-device-detect';
import {
    STATE_ID_CODES, 
    STATE_POPULATIONS, 
    STATE_POPULATIONS_MIL,
    STATE_NAMES
} from '../../constants';
import { Row, Col } from "reactstrap";

class CoronaIndia extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = 
        {
            time_series : null,
            current_number : null,
        }
    }
    componentDidMount()
    {
        Axios.get('https://api.covid19india.org/data.json')
        .then( response => {
            let series = response.data['cases_time_series'];
            let tests = response.data['tested'];
            let curr = response.data['statewise'][0];
            let cr_status = {
                ...curr,
                ...tests[tests.length-1]
            };
            let topstates = response.data['statewise'];
            Axios.get('https://api.covid19india.org/v3/min/data.min.json')
            .then( response => {
                let statewise = {};
                Object.values(STATE_ID_CODES).map((element,index) => {
                    statewise[element] = {};  return null;
                });
                let options = ['confirmed', 'deceased', 'recovered', 'tested'];
                Object.keys(response.data).map((key, index) => {
                    if(key in STATE_ID_CODES)
                    {
                        let Obj = response.data[key]['total'];
                        options.forEach(e => {
                            if(Obj !== undefined && e in Obj) statewise[STATE_ID_CODES[key]][e] = Obj[e];
                            else statewise[STATE_ID_CODES[key]][e] = 0;
                        });
                        statewise[STATE_ID_CODES[key]]['population'] = STATE_POPULATIONS[key];
                        statewise[STATE_ID_CODES[key]]['ppm'] = STATE_POPULATIONS_MIL[key];
                        statewise[STATE_ID_CODES[key]]['state'] = STATE_NAMES[key];
                        statewise[STATE_ID_CODES[key]]['statecode'] = key;
                    }
                    else if(key === 'LA')
                    {
                        let Obj = response.data['LA']['total'];
                        options.forEach(e => {
                            if(Obj !== undefined && e in Obj) statewise[STATE_ID_CODES['JK']][e] += Obj[e];
                        });
                        statewise[STATE_ID_CODES['JK']]['population'] += STATE_POPULATIONS[key];
                        statewise[STATE_ID_CODES['JK']]['ppm'] += STATE_POPULATIONS_MIL[key];
                    }
                });
                let flt = (e) => Number.parseFloat(e);
                Object.keys(statewise).map(key => {
                    statewise[key]['mor'] = (flt(statewise[key]['deceased']*1e2)/flt(statewise[key]['confirmed'])).toFixed(3);
                    statewise[key]['tpm'] = (flt(statewise[key]['tested']*1e6)/flt(statewise[key]['population'])).toFixed(1);
                    statewise[key]['ppm'] = (flt(statewise[key]['confirmed']*1e6)/flt(statewise[key]['population'])).toFixed(1);
                });
                this.setState({
                    time_series : series,
                    current_number : cr_status,
                    topstates : topstates,
                    statewise: statewise
                });
            });
        });
    }
    
    RenderWhenNotNull = (flag) => {
        if(!flag) return null;
        this.maps2 = []; this.maps3 = [];
        let flt = (e) => Number.parseFloat(e);
        let cnf2 = [];
        
        Object.keys(STATE_NAMES).map(element => {
            if(STATE_ID_CODES[element] !== undefined)
            cnf2.push({id: element,
                 state: STATE_NAMES[element],
                 value: this.state.statewise[STATE_ID_CODES[element]]['confirmed']
                });
        }); 
        Object.keys(this.state.statewise).map(element => {
            cnf['data'].push({id : element, value : this.state.statewise[element]['confirmed']});
            rec['data'].push({id : element, value : this.state.statewise[element]['recovered']});
            dec['data'].push({id : element, value : this.state.statewise[element]['deceased']});
            pop['data'].push({id : element, value : this.state.statewise[element]['population']});
            ppm['data'].push({id : element, 
                value : flt(this.state.statewise[element]['confirmed']*1e6)/flt(this.state.statewise[element]['population'])});
            mor['data'].push({id : element, 
                value : flt(this.state.statewise[element]['deceased']*1e2)/flt(this.state.statewise[element]['confirmed'])});
            tst['data'].push({id : element, value : this.state.statewise[element]['tested']});
            tpm['data'].push({id : element, 
                value : flt(this.state.statewise[element]['tested']*1e6)/flt(this.state.statewise[element]['population'])});
        });
        let inf = a => Number.parseFloat(a);
        let int = a => (Number.parseInt(a)).toLocaleString();
        cnf['sideNote'] = int(this.state.current_number['confirmed']);
        rec['sideNote'] = int(this.state.current_number['recovered']);
        dec['sideNote'] = int(this.state.current_number['deaths']);
        tst['sideNote'] = int(this.state.current_number['totalsamplestested']);
        mor['sideNote'] = ((inf(dec['sideNote'])*100.0)/inf(cnf['sideNote'])).toFixed(3);
        tpm['sideNote'] = ((inf(this.state.current_number['totalsamplestested'])*1000000.0)/inf('1332900000')).toFixed(2);
        ppm['sideNote'] = ((inf(this.state.current_number['confirmed'])*1000000.0)/inf('1332900000')).toFixed(2);

        this.maps1 = [cnf, rec, dec, tst];
        //console.log(cnf2);
        if(flag) return(
            <>
            <NumberPanel data={this.state.current_number}/>
            <Row >
                <Col xs='12' sm='12' md='7'>
                    <AllInOneGraph data={this.state.time_series}/>
                </Col>
                <Col xs="12" sm="12" md="5">
                    <Top10India data={this.state.statewise} number={5} type="confirmed" text="Confirmed"/>
                </Col>
            </Row>
            <Row >
                <Col xs='12' md='7'>
                    <StateWiseData style={{maxHeight: '700px'}} data={this.state.topstates}/>
                </Col>
                <Col xs='12' md='5'>
                    <Row>
                        <Col  xs='12' lg='12'>
                            <BrowserView>
                                {this.maps1.map((element,index) => {
                                    return (
                                        <IndiaMap  key={index} label={0} data={element} height={'370px'} /> 
                                    );
                                })}
                            </BrowserView>
                        </Col>
                    </Row>
                </Col>
            </Row>
            <Row>
                <Col xs='12' sm='12' md='5'>
                   <BrowserView><IndiaMap  data={mor} height={'410px'}/></BrowserView>
                </Col>
                <Col xs="12" sm="12" md="7">
                    <Top10India data={this.state.statewise} label={0} number={6} type="mor" text="Mortality Rate"/>       
                </Col>     
            </Row>
            <Row>
                <Col xs="12" sm="12" md="7">
                    <Top10India data={this.state.statewise} label={0} number={6} type="tpm" text="Tested Per Million"/>       
                </Col>   
                <Col xs='12' sm='12' md='5'>
                   <BrowserView><IndiaMap  data={tpm} height={'410px'}/></BrowserView>
                </Col>  
            </Row>
            <Row>
                <Col xs='12' sm='12' md='5'>
                   <BrowserView><IndiaMap  data={ppm} height={'410px'}/></BrowserView>
                </Col>
                <Col xs="12" sm="12" md="7">
                    <Top10India data={this.state.statewise} label={0} number={6} type="ppm" text="Confirmed Per Million"/>       
                </Col>     
            </Row>
            </>
        );
    }
    render()
    { 
        return(
            <div className='content'>
                {this.RenderWhenNotNull(this.state.statewise)}
            </div>
        );
    }
}

export default CoronaIndia;