import React from 'react';
import { Col, Card, CardBody, CardHeader, Row } from 'reactstrap';
import DataTable, { createTheme } from 'react-data-table-component';
 
class CountryWiseData extends React.Component
{
    constructor(props)
    {
        super(props);
    }
    currentTheme(){
        const body = document.body.classList;
        let white = false;
        Object.keys(body).map((e) => {if(body[e] === 'white-content') white = true;})
        return white;
    }
    render()
    {
        let int = (str) => {
            if(str === undefined) return 0;
            let e = "";
            for(let i=0; i<str.length; i++)
            {
                if(str[i] - '0' >= 0 || str[i] - '0' <= 9) e+=str[i];
                if(str[i] == '.') break;
            }
            return Number.parseInt(e);
        }
        const theme = this.currentTheme();
        createTheme('solarized', {
            text: {
              primary: !theme ? "#f2f2f2" : "#333333",
              secondary: '#2aa198',
            },
            background: {
              default: !theme ? "#27293d" : "#ffffff",
            },
            divider:{
                default: !theme ? "#666666" : "#cccccc",
            },
            context: {
              background: '#cb4b16',
              text: '#FFFFFF',
            },
            action: {
              button: 'rgba(0,0,0,.54)',
              hover: 'rgba(0,0,0,.08)',
            },
          });
        let data = [];
        let total = {};
        this.props.data.forEach((element,index) => {
            let cnf_str;
            if(int(element['NewCases']) > 0) cnf_str = <div>
                <div style={{color: '#ff3333', fontSize: '12px', textAlign:'center'}}>{element['NewCases']}</div>
                <div>{element['TotalCases']}</div></div>
            else cnf_str = <div><div>{element['TotalCases']}</div></div>
            let rec_str;
            if(int(element['NewRecovered']) > 0) rec_str = <div>
                <div style={{color: 'green', fontSize: '12px', textAlign:'center'}}>{(element['NewRecovered'])}</div>
                <div>{element['TotalRecovered']}</div></div>
            else rec_str = <div><div>{element['TotalRecovered']}</div></div>
            let dec_str;
            if(int(element['NewDeaths']) > 0) dec_str = <div>
                <div style={{color: 'gray', fontSize: '12px', textAlign:'center'}}>{(element['NewDeaths'])}</div>
                <div>{element['TotalDeaths']}</div></div>
            else dec_str = <div><div>{element['TotalDeaths']}</div></div>
            let ele = {id : index,
                country : element['Country'],
                confirmed :   cnf_str,
                confirmedsor : int(element['TotalCases']),
                active : element['ActiveCases'],
                activesor : int(element['ActiveCases']),
                recovered : rec_str,
                recoveredsor : int(element['TotalRecovered']),
                deaths : dec_str,
                deathssor : int(element['TotalDeaths']),
                tests : element['TotalTests'],
                testssor : int(element['TotalTests']),
                cpm : element['TotCases_1M_Pop'],
                cpmsor : int(element['TotCases_1M_Pop']),
                dpm : element['Deaths_1M_pop'],
                dpmsor : int(element['Deaths_1M_pop']),
                tpm : element['Tests_1M_Pop'],
                tpmsor : int(element['Tests_1M_Pop'])
               };
            if(index != 0)
            data.push(ele);
            else total = ele;
        });
        data.push(total);
        const columns = [
        {
            name: 'Country',
            selector: 'country',
            wrap: true,
            center:false,
            sortable:'true',
            width: '16%',
        },
        {
            name: 'Cofirmed',
            selector: 'confirmed',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '11%',
        },
        {
            name: 'Active',
            selector: 'active',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '11%',
        },
        {
            name: 'Recovered',
            selector: 'recovered',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '10%',
        },
        {
            name: 'Deaths',
            selector: 'deaths',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '10%',
        },
        {
            name: 'Tests',
            selector: 'tests',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '12%',
        },
        {
            name: 'Cases/1M',
            selector: 'cpm',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '10%',
        },
        {
            name: 'Deaths/1M',
            selector: 'dpm',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '10%',
        },
        {
            name: 'Tests/1M',
            selector: 'tpm',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '10%',
        }
        ];
        const customStyles = {
            rows: {
              style: {
                minHeight: '45px',
                fontSize: '15px',
                width: '100%'
              }
            },
            headCells: {
                style: {
                    fontSize: '17px',
                    fontWeight: 400,
                    color: !theme ? "#333333" : "#262626",
                    background: '#cccccc',
                    width: '100%'
                }
            },
            cells: {
                style: {
                  width: '100%',
                  align: 'left',
                },
              },
          }; 
        this.sortFunction = (rows,field,direction) => {
            if(field == 'country') 
            rows.sort((a,b) => ((direction === 'desc') ? -1 : 1) * a[field].localeCompare(b[field]));
            else rows.sort((a,b) => {
                if(direction === 'asc') return a[field+'sor']-b[field+'sor'];
                else return b[field+'sor']-a[field+'sor'];
            });
            let ele = [];
            let tot = {};
            rows.forEach(element => {
                ele.push(element);
            });
            //ele.push(tot);
            console.log(ele);
            return ele;
        };
            
        return (
            <>
            <Card className="card-chart">
                <CardBody style={{padding: '2% 2%'}}>
                <DataTable
                    title='Statewise Data'
                    columns={columns}
                    data={data}
                    theme="solarized"
                    customStyles={customStyles}
                    highlightOnHover= {true}
                    pagination={true}
                    paginationPerPage={15}
                    paginationRowsPerPageOptions={[15,50,100]}
                    sortFunction={this.sortFunction}
                />
                </CardBody>
              </Card>
            </>
        );
    }
}

export default CountryWiseData;