import React from 'react';

import FusionCharts from "fusioncharts";
import Maps from 'fusioncharts/fusioncharts.maps';
import India from 'fusioncharts/maps/fusioncharts.india';
import charts from "fusioncharts/fusioncharts.charts";
import ReactFusioncharts from "react-fusioncharts";
import {Card, CardBody, CardHeader} from 'reactstrap';

// Resolves charts dependancy
ReactFusioncharts.fcRoot(FusionCharts,Maps,India,charts);

class IndiaMap extends React.Component {
  constructor(props)
  {
    super(props);
    let d = this.props.data;
    const theme = this.currentTheme();
    this.dataSource = {
      chart: {
        numbersuffix: ".",
        includevalueinlabels: "0",
        showlabels : "0",
        showLegend : "0",
        labelsepchar: ": ",
        showmarkerlabels: "0",
        bgColor: !theme ? "#27293d" : "#ffffff",
        showBorder:'1',
        borderAlpha:'30',
        showCanvasBorder:'0',
        "entityFillHoverColor": "#E5E5E9",
      },
      colorrange: {
        minvalue: "0",
        code: "#ffffff",
        gradient: "1",
        "color": d['color']
      },
      data: d['data']
    };
  }
  currentTheme(){
    const body = document.body.classList;
    let white = false;
    Object.keys(body).map((e) => {if(body[e] === 'white-content') white = true;})
    return white;
  }
  render() {
    return (
        <Card className="card-chart">
            <CardHeader>
                <h3 className="card-category" style={{marginBottom : '0px'}}>
                <span style={{fontSize : "20px", textAlign : "left"}}>
                  {this.props.data['title']} </span>
                <span className='pull-right' style={{fontSize : "18px", textAlign : "right"}}>
                  {this.props.data.sideNote} </span>
                </h3>
            </CardHeader>
            <CardBody>
                <div className="chart-area" style={{height: this.props.height}} >
                <ReactFusioncharts
                    type="india"
                    height="89%"
                    width="105%"
                    dataFormat="JSON"
                    dataSource={this.dataSource}
                  
                />
                </div>
            </CardBody>
        </Card>
    );
  }
}

export default IndiaMap;