import React from "react";
import { CSSTransition } from 'react-transition-group';
import CommaNumber from 'comma-number';
//import '../../../assets/css/dummy.css';
import {
  Card,
  CardHeader,
  CardTitle,
  Row,
  Col
} from "reactstrap";


class NumberPanel extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            showMessage : true
        };
    }
    
    render(){
        if(this.props.data == null) return null;
        let confirmed = CommaNumber(this.props.data['confirmed']);
        let deceased = CommaNumber(this.props.data['deaths']);
        let recovered = CommaNumber(this.props.data['recovered']);
        let active = CommaNumber(this.props.data['active']);
        let dcon = Number.parseInt(this.props.data['deltaconfirmed']);
        let drec = Number.parseInt(this.props.data['deltarecovered']);
        let ddec = Number.parseInt(this.props.data['deltadeaths']);
        let dact = dcon - drec - ddec;
        let dconfirmed = "[ +"+CommaNumber(this.props.data['deltaconfirmed'])+" ]";
        let ddeceased = "[ +"+CommaNumber(this.props.data['deltadeaths'])+" ]";
        let drecovered = "[ +"+CommaNumber(this.props.data['deltarecovered'])+" ]";
        let dactive = "[ +"+CommaNumber(dact)+" ]";
        let today_test = "[ +"+CommaNumber(this.props.data.samplereportedtoday)+" ]";
        let tests = CommaNumber(this.props.data['totalsamplestested']);
        let listContent = [
            [2, "Confirmed", "text-danger", confirmed, dconfirmed],
            [2, "Active", "text-primary", active, dactive],
            [2, "Recovered", "text-success", recovered, drecovered],
            [2, "Deceased", "text-defalut", deceased, ddeceased],
            [2, "Population", "text-warning", "1.378 B", ""],
            [2, "Tests", "text-info", tests, today_test]
        ];
        //const link
        const list = listContent.map((ele, key) => {
            let link = null, update= null;
            if(key == 5) link = <a href={this.props.data['source']} target="_blank">
                    <i className={"pull-right tim-icons icon-attach-87 "+ele[2]} />
                </a>
            if(ele[4].length != 0)
            {
                update = <div style={{fontSize : "15px"}}>{ele[4]}</div>;
            }
            else update = <div style={{fontSize : "15px"}}><br></br></div>;
            return (
                <Col lg={ele[0]} key={key}>
                <Card className="card-chart">
                    <CardHeader>
                    <h1 className="card-category">
                        <span className="card-big-text"> {ele[1]} </span>
                        {link}
                    </h1>
                    {update}
                    <CardTitle tag="h4">
                        <i className={"tim-icons icon-world "+ele[2]} />{" "}
                        {ele[3]}
                    </CardTitle>
                    </CardHeader>
                </Card>
                </Col>
            );
        });
        return (
      
            <>
                <CSSTransition
                    in={this.state.showMessage}
                    timeout={1000}
                    appear={true}
                    classNames="fader"
                >
                    <Row>
                        {list}
                    </Row>
                </CSSTransition>
            </>

        );
    }
}

export default NumberPanel;