import React from 'react';
import Axios from 'axios';
import Loader from 'react-loader-spinner';
import NumberPanel from "./segments/NumberPanel";
import CountryWiseData from './segments/CountryWiseData';
import { Row, Col } from 'reactstrap';

const ls = require('local-storage');
class CoronaWorld extends React.Component{
    constructor(props)
    {
        super(props);
        this.graphCss = {width: '100%', height: '500px', border: '0px none'};
        let fetcher = false, world_data = null, countrywise_data = null;
        if(!ls.get('covid19worldtime') || !ls.get('covid19worlddata') || Date.now() - ls.get('covid19worldtime') > 2000000)
        {
            ls.set('covid19worldtime',Date.now());
            fetcher = true;
        }
        if(!fetcher) 
        {
            world_data = ls.get('covid19worlddata');
            countrywise_data = ls.get('covid19countrywisedata');
        }
        this.state = 
        {
            world_data : world_data,
            countrywise_data : countrywise_data,
            loader : fetcher,
            fetcher : fetcher
        }
        
    }

    componentDidMount()
    {
        if(this.state.fetcher)
        {
            Axios.get('https://covid19api.io/api/v1/AllReports')
            .then( response => {
                ls.set('covid19worlddata',response.data['reports'][0]['table'][0][0]);
                ls.set('covid19countrywisedata',response.data['reports'][0]['table'][0].slice(0,-1));
                this.setState({
                    world_data : response.data['reports'][0]['table'][0][0],
                    countrywise_data : response.data['reports'][0]['table'][0].slice(0,-1),
                    loader: false
                });
            });
        }
    }

    RenderWhenNotNull = (flg) => {
        console.log(this.state.world_data);
        if(flg)
        return (
            <div>
                <NumberPanel data={this.state.world_data} />
                <Row style={{marginBottom : '30px'}}>
                    <Col sm='12' md='6'>
                    <iframe src="https://ourworldindata.org/grapher/total-cases-covid-19?country=BRA~IND~IRN~ITA~RUS~ESP~GBR~USA" 
                     style={this.graphCss}></iframe>
                    </Col>
                    <Col sm='12' md='6'>
                    <iframe src="https://ourworldindata.org/grapher/total-deaths-covid-19?country=BEL~BRA~CAN~FRA~IND~IRN~ITA~MEX~RUS~GBR~USA" 
                     style={this.graphCss}></iframe>
                    </Col>
                </Row>
                <Row>
                    <Col xs='12' md='12'>
                        <CountryWiseData data={this.state.countrywise_data} />
                    </Col>
                </Row>
                <Row style={{marginBottom : '30px'}}>
                    <Col sm='12' md='6'>
                    <iframe src="https://ourworldindata.org/grapher/daily-covid-cases-3-day-average?country=FRA~DEU~IND~ITA~RUS~KOR~ESP~GBR~USA~BRA" 
                    loading="lazy" style={this.graphCss}></iframe>
                    </Col>
                    <Col sm='12' md='6'>
                    <iframe src="https://ourworldindata.org/grapher/daily-deaths-covid-19?country=BRA~RUS~GBR~USA~IND~IRN~ITA" 
                    loading="lazy" style={this.graphCss}></iframe>
                    </Col>
                </Row>
                <Row style={{marginBottom : '30px'}}>
                    <Col sm='12' md='6'>
                    <iframe src="https://ourworldindata.org/grapher/total-confirmed-cases-of-covid-19-per-million-people" 
                    loading="lazy" style={this.graphCss}></iframe>
                    </Col>
                    <Col sm='12' md='6'>
                    <iframe src="https://ourworldindata.org/grapher/total-covid-deaths-per-million" 
                    loading="lazy" style={this.graphCss}></iframe>
                    </Col>
                </Row>
                <Row style={{marginBottom : '30px'}}>
                    <Col sm='12' md='6'>
                    <iframe src="https://ourworldindata.org/grapher/coronavirus-cfr?country=BRA~DEU~IND~ITA~RUS~KOR~ESP~OWID_WRL" 
                    loading="lazy" style={this.graphCss}></iframe>
                    </Col>
                    <Col sm='12' md='6'>
                    <iframe src="https://ourworldindata.org/grapher/number-of-covid-19-tests-per-confirmed-case?country=IDN~CAN~DEU~SWE~TWN~USA~KOR~ITA~VNM~JPN~IND~ZAF~NZL~AUS~PHL~GBR~SGP~ARG~MEX~BRA" 
                    loading="lazy" style={this.graphCss}></iframe>
                    </Col>
                </Row>
                <Row style={{marginBottom : '5px'}}>
                    <Col sm='12' md='12'>
                    <iframe src="https://ourworldindata.org/grapher/full-list-cumulative-total-tests-per-thousand-map" 
                    loading="lazy" style={this.graphCss}></iframe>
                    </Col>
                </Row>
            </div>
        );
    }

    Loading = (flag) => {
        if(flag)
        return(
            <div style={{marginLeft: '45%', marginTop: '20%' }}>
            <Loader
                type="TailSpin"
                color="#00BFFF"
                height={100}
                width={100}
                timeout={5000}
            /></div>
        );
    }

    render()
    {
        return (
            <div className='content'>
                {this.Loading(this.state.loader)}
                {this.RenderWhenNotNull(this.state.world_data)}
            </div>
        );
    }
}
export default CoronaWorld;