import React from 'react';
import { Col, Card, CardBody, CardHeader, Row } from 'reactstrap';
import DataTable, { createTheme } from 'react-data-table-component';
 
class StateWiseData extends React.Component
{
    constructor(props)
    {
        super(props);
    }
    currentTheme(){
        const body = document.body.classList;
        let white = false;
        Object.keys(body).map((e) => {if(body[e] === 'white-content') white = true;})
        return white;
    }
    render()
    {
        const theme = this.currentTheme();
        createTheme('solarized', {
            text: {
              primary: !theme ? "#f2f2f2" : "#333333",
              secondary: '#2aa198',
            },
            background: {
              default: !theme ? "#27293d" : "#ffffff",
            },
            divider:{
                default: !theme ? "#666666" : "#cccccc",
            },
            context: {
              background: '#cb4b16',
              text: '#FFFFFF',
            },
            action: {
              button: 'rgba(0,0,0,.54)',
              hover: 'rgba(0,0,0,.08)',
            },
          });
        let data = [];
        let int = (a) => Number.parseInt(a);
        let intf = (a) => Number.parseInt(a).toLocaleString();
        let total = {};
        this.props.data.forEach((element,index) => {
            let cnf_str;
            if(int(element['deltaconfirmed']) > 0) cnf_str = <div>
                <div style={{color: '#ff3333', fontSize: '12px', textAlign:'center'}}>+{intf(element['deltaconfirmed'])}</div>
                <div>{intf(element['confirmed'])}</div></div>
            else cnf_str = <div><div>{intf(element['confirmed'])}</div></div>
            let rec_str;
            if(int(element['deltarecovered']) > 0) rec_str = <div>
                <div style={{color: 'green', fontSize: '12px', textAlign:'center'}}>+{intf(element['deltarecovered'])}</div>
                <div>{intf(element['recovered'])}</div></div>
            else rec_str = <div><div>{intf(element['recovered'])}</div></div>
            let dec_str;
            if(int(element['deltadeaths']) > 0) dec_str = <div>
                <div style={{color: 'gray', fontSize: '12px', textAlign:'center'}}>+{intf(element['deltadeaths'])}</div>
                <div>{intf(element['deaths'])}</div></div>
            else dec_str = <div><div>{intf(element['deaths'])}</div></div>
            let ele = {id : index,
                state : element['state'],
                confirmed :   cnf_str,
                confirmedsor : int(element['confirmed']),
                active : intf(element['active']),
                activesor : int(element['active']),
                recovered : rec_str,
                recoveredsor : int(element['recovered']),
                deaths : dec_str,
                deathssor : int(element['deaths'])
               };
            if(index != 0)
            data.push(ele);
            else total = ele;
        });
        data.push(total);
        const columns = [
        {
            name: 'States / UT',
            selector: 'state',
            wrap: true,
            center:false,
            sortable:'true',
            width: '30%',
        },
        {
            name: 'Cofirmed',
            selector: 'confirmed',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '18%',
        },
        {
            name: 'Active',
            selector: 'active',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '18%',
        },
        {
            name: 'Recovered',
            selector: 'recovered',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '18%',
        },
        {
            name: 'Deaths',
            selector: 'deaths',
            sortable: true,
            allowOverflow:true,
            center:true,
            width: '16%',
        }
        ];
        const customStyles = {
            rows: {
              style: {
                minHeight: '45px',
                fontSize: '15px',
                width: '100%'
              }
            },
            headCells: {
                style: {
                    fontSize: '17px',
                    fontWeight: 400,
                    color: !theme ? "#333333" : "#262626",
                    background: '#cccccc',
                    width: '100%'
                }
            },
            cells: {
                style: {
                  width: '100%',
                  align: 'left',
                },
              },
          }; 
        this.sortFunction = (rows,field,direction) => {
            if(field == 'state') 
            rows.sort((a,b) => ((direction === 'desc') ? -1 : 1) * a[field].localeCompare(b[field]));
            else rows.sort((a,b) => {
                if(direction === 'asc') return a[field+'sor']-b[field+'sor'];
                else return b[field+'sor']-a[field+'sor'];
            });
            let ele = [];
            let tot = {};
            rows.forEach(element => {
                if(element.id == 0) tot = element;
                else ele.push(element);
            });
            ele.push(tot);
            return ele;
        };
            
        return (
            <>
            <Card className="card-chart">
                <CardBody style={{padding: '2% 2%'}}>
                <DataTable
                    title='Statewise Data'
                    columns={columns}
                    data={data}
                    theme="solarized"
                    customStyles={customStyles}
                    highlightOnHover= {true}
                    sortFunction={this.sortFunction}
                />
                </CardBody>
              </Card>
            </>
        );
    }
}

export default StateWiseData;